"use client";

import { useState } from "react";

export function ResponsiveNav() {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <nav className="flex justify-between items-center p-4 bg-white shadow-md relative">
      <button
        id="menuBtn"
        className="md:hidden px-4 py-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
        onClick={() => setMenuOpen(!menuOpen)}
        aria-expanded={menuOpen}
        aria-controls="menu"
      >
        Menu
      </button>

      <ul
        id="menu"
        className={`transition-transform duration-300 ease-in-out gap-8
          md:flex md:flex-row md:static md:translate-y-0 md:bg-transparent
          ${menuOpen 
            ? "flex flex-col absolute top-16 left-0 w-full bg-white translate-y-0"
            : "hidden md:flex"
          }`}
      >
        <li className="px-4 py-2 hover:underline">Home</li>
        <li className="px-4 py-2 hover:underline">About</li>
        <li className="px-4 py-2 hover:underline">Contact</li>
      </ul>
    </nav>
  );
}
